body {
  min-height: 100vh;
  background: white;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

button {
  cursor: pointer;
}

.header {
  background: #eee;
  text-align: center;
}

.header-content {
  position: relative;
  max-width: 700px;
  margin: auto;
}

.nav {
  text-align: center;
  padding: 10px;
}

.nav__item {
  border: none;
  margin: 5px 10px;
  padding: 5px;
}

.nav__item-text {
  padding-bottom: 3px;
  border-bottom: 2px solid transparent;
  -webkit-transition: border 0.4s;
  transition: border 0.4s;
}

.nav__item--selected .nav__item-text {
  border-bottom: 2px solid black;
}

.tools {
  position: absolute;
  left: 0;
  top: 50%;
  padding-left: 15px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.help-button {
  border-radius: 100%;
  width: 30px;
  height: 30px;
  border: 1px solid #aaa;
}

.content {
  padding-top: 20px;
  margin: auto;
  max-width: 700px;
}

.button {
  box-shadow: inset 0 -2px 0 #aaaaaa, inset 0px 1px 1px -1px #fff, 0px 1px 1px 0px #7a7a7a;
  border-radius: 3px;
  border-radius: 3px;
  width: 120px;
  font-size: 14px;
  padding: 10px;
  margin-right: 10px;
  border: none;
  /* transition: transform 0.08s ease, background-color 0.08s ease-in-out, box-shadow 0.08s ease; */
}

/* adding animation for button click */
.button--not-disabled:active {
  box-shadow: none;
  -webkit-transform: translateY(2px);
          transform: translateY(2px);
}


.guess-section {
  text-align: center;
  font-size: 20px;
  padding: 15px;
  margin-bottom: 20px;
}

.english-word {
  margin-bottom: 20px;
}

.arabic-input {
  text-align: center;
  font-size: 30px;
  padding: 6px;
  border-radius: 3px;
  border: none;
  border-bottom: 1px solid grey;
  margin-bottom: 10px;
}

.arabic-input.correct {
  -webkit-animation: fade-out 1.5s;
          animation: fade-out 1.5s;
}

.show-answer {
  height: 30px;
  margin-bottom: 10px;
}

@-webkit-keyframes fade-out {
  0% {
    background-color: none;
  }
  10% {
    background-color: #98ff98;
  }
  100% {
    background-color: none;
  }
}

@keyframes fade-out {
  0% {
    background-color: none;
  }
  10% {
    background-color: #98ff98;
  }
  100% {
    background-color: none;
  }
}
.key.unused {
  background-color: #ccc; }

.english-character {
  position: absolute;
  top: 3px;
  left: 3px;
  color: #aaa;
  font-size: 0.6em; }

.container {
  max-width: 674px;
  padding: 8px 8px;
  margin: 0 auto 30px auto;
  box-shadow: inset 1px 1px 0px #f1f1f1, 1px 1px 1px 1px #6a6a7b;
  background-color: #e3e3e3;
  border-radius: 5px;
}

.keyboard-row {
  display: flex;
}

.keyboard-row.row-1 {
  max-width: 672px; /* 56 * 12 */
}

.keyboard-row.row-2 {
  max-width: 560px; /* 56 * 10 */
  margin-left: 18px;
}

.keyboard-row.row-3 {
  max-width: 392px; /* 56 * 7 */
  margin-left: 46px;
}

.key {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  text-align: center;
  padding: 3px 2px;
  height: 30px;
  margin: 4px;
  letter-spacing: 0.5px;
  color: #3a3a3a;
  background-color: #f7f7f7;
  flex: 1 1;
  box-shadow: inset 0 -2px 0 #aaaaaa, inset 0px 1px 1px -1px #fff, 0px 1px 1px 0px #7a7a7a;
  border-radius: 3px;
  text-transform: uppercase;
  font-size: 20px;
  line-height: 1.3;
  white-space: nowrap;
  overflow: hidden;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-transition: background-color 0.08s ease-in-out, box-shadow 0.08s ease, -webkit-transform 0.08s ease;
  transition: background-color 0.08s ease-in-out, box-shadow 0.08s ease, -webkit-transform 0.08s ease;
  transition: transform 0.08s ease, background-color 0.08s ease-in-out, box-shadow 0.08s ease;
  transition: transform 0.08s ease, background-color 0.08s ease-in-out, box-shadow 0.08s ease, -webkit-transform 0.08s ease;
  cursor: pointer;
}

.key.light {
  background-color: #b2e4ff;
  box-shadow: none;
  -webkit-transform: translateY(2px);
          transform: translateY(2px);
}

.key span {
  display: inline-block;
  vertical-align: middle;
}

.key::before {
  display: inline-block;
  vertical-align: middle;
  height: 100%;
  content: '';
}

@media (min-width: 640px) {
  .key {
    height: 48px;
  }
}
.cards-table {
  width: 60%;
  margin: auto;
  text-align: center;
}

.cards-table td {
  padding: 7px 15px;
}

.cards-table th {
  padding: 7px 15px;
}

.en-col {
  text-align: right;
}

.ar-col {
  text-align: left;
}
.add-form__field {
  text-align: center;
  display: block;
  padding: 10px;
  margin-bottom: 10px;
}

.add-form__input {
  margin-left: 20px;
  padding: 5px 0;
  border: none;
  border-bottom: 1px solid grey;
}

.add-form__submit-button {
  display: block;
  margin: 20px auto 30px auto;
}

.message {
  text-align: center;
  margin-bottom: 20px;
}
